.App {
  text-align: center;
  font-family: Helvetica, Arial, sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-content {
  font-size: large;
  text-align: left;
  padding: 20px 0;
}

form {
  max-width: 800px;
  display: block;
  margin: auto;
}

form fieldset {
  display: block;
  border: none;
  padding: 10px;
}

form label {
  display: inline-block;
}

form .label-wrap {
  display: inline-block;
  font-weight: 400;
  margin-bottom: 8px;
  cursor: pointer;
}

form input {
  padding: 8px;
  font-size: inherit;
  font-family: inherit;
  font-weight: 300;
  border: 2px solid #f3f3f3;
}

form input:hover {
  border: 2px solid #dfdfdf;
}

form input:focus {
  outline: none;
  border: 2px solid #60dafb;
}

form .input-wrap {
  width: auto;
  margin: auto;
}

form .input--error input {
  border-color: red;
}

form .input--error .input-error-text {
  color: red;
  margin-top: 10px;
  font-size: 0.8rem;
}

.state-preview {
  margin: 10px;
  padding: 16px;
  border-radius: 4px;
  background-color: #fafafa;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



